@import "../../inc/vars";
.acrobits_images_with_text {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	align-items: center;
	padding: 40px 0;
	position: relative;
	z-index: 2;
	&__image {
		width: 100%;
		order: 2;
		padding: 0 15px;
		position: relative;
		z-index: 2;
		@media (min-width: $screen-sm) {
			width: 55%;
			order: -1;
		}
		img {
			max-width: 100%;
			height: auto;
			@media (min-width: $screen-sm) {
				margin-left: -23%;
			}
		}
	}
	&__content {
		width: 100%;
		padding: 30px;
		position: relative;
		z-index: 2;
		@media (min-width: $screen-sm) {
			width: 45%;
			padding-right: 80px;
		}
	}
	&__title {
		@include titleGrad();
		@include pop(400, 35, 1.24);
		letter-spacing: -0.7px;
		margin-bottom: 24px;
		width: 506px;
		max-width: 100%;
		@media (min-width: $screen-sm){
			@include pop(400, 44, 1.12);
			letter-spacing: -0.88px;
			margin-bottom: 16px;
		}
	}
	&__subtitle {
		@include fig(400, 18, 1.24);
		color: #5A6272;
		margin-bottom: 16px;
		width: 506px;
		max-width: 100%;
		@media (min-width: $screen-sm){
			@include fig(400, 25, 1.56);
		}
	}
	&__description {
		@include fig(400, 20, 1.24);
		color: #4F4B58;
		margin-bottom: 24px;
		width: 506px;
		max-width: 100%;
		@media (min-width: $screen-sm){
			line-height: 156%;
		}
	}
	.button {
		@include greenButton();
	}
	&_style_1 {
		position: relative;
		&:before {
			@media (min-width: $screen-sm){
				content: '';
				display: block;
				position: absolute;
				width: 1595px;
				height: 1436px;
				border-radius: 1595px;
				background: #FFF;
				filter: blur(170px);
				right: -30%;
				top: 50%;
				transform: translateY(-50%);
			}
		}
	}
	&_style_2 {
		@media (min-width: $screen-mlg) {
			padding-left: 100px;
			padding-right: 100px;
		}
		.acrobits_images_with_text__image {
			text-align: center;
			img {
				width: 516px;
				max-width: 100%;
				height: auto;
				margin: 0;
			}
		}
	}
	&.acrobits_images_with_text_style_1 {
		.acrobits_images_with_text__image {
			@media (min-width: $screen-sm){
				img {
					max-width: 123%;
				}
			}
		}
	}
	&_style_3 {
		padding-top: 16px;
		padding-bottom: 40px;
		border-bottom: 1px #D1D5DB solid;
		@media (min-width: $screen-sm){
			padding-top: 60px;
			padding-bottom: 60px;
		}
		.acrobits_images_with_text__image {
			order: 2;
			padding: 0 20px;
			width: 100%;
			img {
				margin: 0;
			}
			@media (min-width: $screen-sm){
				width: 45%;
				padding: 0;
				img {
					margin-bottom: -100px;
				}
			}

		}
		.acrobits_images_with_text__content {
			padding: 0 20px;
			width: 100%;
			@media (min-width: $screen-sm){
				padding: 0 30px 0 30px;
				width: 55%;
			}
			@media (min-width: $screen-md){
				padding: 0 30px 0 100px;
			}
			.acro-title {
				text-align: center;
				margin-bottom: 16px;
				@include pop(400, 34, 1.24);
				@media (min-width: $screen-sm){
					text-align: left;
					@include pop(400, 44, 1.12);
				}
			}
		}
	}
	&_style_4 {
		padding: 60px 20px;
		width: 1400px;
		max-width: 100%;
		margin: 0 auto;
		@media (min-width: $screen-sm){
			padding: 120px 20px;
		}
		.acrobits_images_with_text__image {
			order: 2;
			img {
				margin: 0;
			}
		}
		.acrobits_images_with_text__content {
			padding: 0;
			display: flex;
			flex-direction: column;
			justify-content: center;
			@media (min-width: $screen-sm){
				justify-content: flex-start;
			}
		}
		.acro-title {
			line-height: 124%;
			letter-spacing: -0.6px;
			font-size: 30px;
			text-align: center;
			margin-bottom: 21px;
			width: 100%;
			@media (min-width: $screen-sm){
				text-align: left;
				font-size: 36px;
				letter-spacing: -0.72px;
				margin-bottom: 35px;
			}
		}
		.acro-decription {
			line-height: 156%;
			font-size: 20px;
			text-align: center;
			margin-bottom: 37px;
			width: 100%;
			@media (min-width: $screen-sm){
				text-align: left;
				font-size: 24px;
				margin-bottom: 57px;
			}
		}
		.acrobits_images_with_text__button {
			height: 46px;
			margin: 0 auto;
			width: auto;
			@media (min-width: $screen-sm){
				height: 64px;
				margin: 0;
				width: 240px;
			}
		}
	}
	&_style_5 {
		padding: 37px 24px;
		width: 1400px;
		max-width: calc(100% - 40px);
		margin: 0 auto;
		background: #fff;
		box-shadow: 0px 2px 4px 0px rgba(7, 4, 146, 0.10), 0px 24px 60px 0px rgba(6, 47, 125, 0.05), 0px 12px 24px 0px rgba(27, 59, 119, 0.05);
		margin-bottom: 43px;
		@media (min-width: $screen-sm){
			padding: 67px 100px;
			margin-bottom: 109px;
		}
		.acrobits_images_with_text__content {
			padding: 0;
			width: 100%;
			@media (min-width: $screen-sm){
				text-align: center;
			}
			.acro-subtitle,
			.acro-title,
			.acro-decription {
				width: 100%;
			}
			.acro-subtitle {
				@include pop(400, 18, 1.36);
				margin-bottom: 21px;
				@media (min-width: $screen-sm){
					font-size: 24px;
					margin-bottom: 6px;
				}
			}
			.acro-title {
				@include pop(400, 28, 1.24);
				letter-spacing: -0.56px;
				margin-bottom: 22px;
				@media (min-width: $screen-sm){
					font-size: 36px;
					margin-bottom: 28px;
				}
			}
			.acro-decription {
				@include pop(400, 20, 1.56);
				margin-bottom: 30px;
				@media (min-width: $screen-sm){
					font-size: 24px;
				}
			}
		}
		.acrobits_images_with_text__image {
			padding: 0;
			width: 100%;
			order: 2;
			text-align: center;
			img {
				margin: 0;
				width: 667px;
			}
		}
	}
	&_style_6 {
		padding: 60px 20px 0;
		width: 1400px;
		max-width: 100%;
		margin: 0 auto;
		background: #321B5F;
		color: #fff;
		@media (min-width: $screen-sm){
			padding: 70px 20px 0;
		}
		.acrobits_images_with_text__image {
			width: 100%;
			order: 2;
			padding: 0;
			display: flex;
			flex-direction: column;
			justify-content: center;
			align-items: center;
			img {
				margin: 0 0 0 7%;
				max-width: 150%;
				@media (min-width: $screen-sm){
					margin: 0;
					max-width: 100%;
				}
			}
		}
		.acrobits_images_with_text__content {
			width: 100%;
			padding: 0;
			text-align: center;
			display: flex;
			flex-direction: column;
			justify-content: center;
			margin-bottom: 28px;
			@media (min-width: $screen-sm){
				margin-bottom: 36px;
				padding: 0 140px;
			}
			.acro-subtitle,
			.acro-title,
			.acro-decription {
				width: 100%;
				color: #fff;
				background: none;
				-webkit-text-fill-color: #fff;
				text-fill-color: #fff;
			}
			.acro-title {
				@include pop(400, 28, 1.24);
				letter-spacing: -0.56px;
				margin-bottom: 25px;
				@media (min-width: $screen-sm){
					font-size: 36px;
					letter-spacing: -0.72px;
					margin-bottom: 23px;
				}
			}
			.acro-decription {
				@include fig(400, 20, 1.56);
				margin-bottom: 36px;
				@media (min-width: $screen-sm){
					font-size: 24px;
					margin-bottom: 39px;
				}
			}
			.acrobits_images_with_text__button {
				height: 46px;
				margin: 0 auto;
				width: auto;
				@media (min-width: $screen-sm){
					height: 64px;
					margin: 0 auto;
					width: 240px;
				}
			}
		}
	}
}